.hero-block {
  position: relative;
  width: 100%;
  height: 100%;
}
.hero-block__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: auto;
  width: 100vw;
  height: 60vh;
  overflow: hidden;
  z-index: -10;
}
.hero-block__wrapper.height--1 {
  height: 100vh;
}
.hero-block__image {
  position: absolute;
  inset: 0;
  will-change: transform;
}
.hero-block__image img {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  transform: scale(1.06);
}
.hero-block__copy {
  position: absolute;
  inset: 0;
  display: grid;
  place-items: center;
  padding: 24px;
  text-align: center;
  pointer-events: none;
}
.hero-block__heading {
  margin: 0;
  max-width: 1100px;
  font-size: clamp(2.4rem, 6vw, 5.5rem);
  line-height: 1.05;
  font-weight: 300;
  letter-spacing: 0.02em;
  text-shadow: 0 10px 30px rgba(0, 0, 0, 0.35);
  will-change: transform;
}
.hero-block__spacer {
  height: 44vh;
}
.hero-block__spacer.height--1 {
  height: 100vh;
}

@supports (animation-timeline: scroll()) {
  .hero-block__image {
    animation-name: heroParallaxImage;
    animation-timing-function: linear;
    animation-fill-mode: both;
    animation-duration: 1ms;
    animation-timeline: scroll(root);
  }
  .hero-block__heading {
    animation-name: heroParallaxHeading;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
    animation-duration: 1ms;
    animation-timeline: scroll(root);
  }
  @keyframes heroParallaxImage {
    from {
      transform: translate3d(0, 0vh, 0) scale(1.06);
    }
    to {
      transform: translate3d(0, -16vh, 0) scale(1.1);
    }
  }
  @keyframes heroParallaxHeading {
    from {
      transform: translate3d(0, 0vh, 0);
    }
    to {
      transform: translate3d(0, -38vh, 0);
    }
  }
}
.hero-block__wrapper:not(.height--1) .hero-block__image {
  animation-name: heroParallaxImage60 !important;
}

@keyframes heroParallaxImage60 {
  from {
    transform: translate3d(0, 0vh, 0) scale(1.06);
  }
  to {
    transform: translate3d(0, -16vh, 0) scale(1.22);
  }
}
